@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&display=swap');

body {
  font-family: 'Oswald', sans-serif;
}

.nui-wrapper {
  text-align: center;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  .mainContainer {

    display: flex;
    flex-direction: column;

    background: linear-gradient(0deg, rgba(0, 0, 0, 0.85), rgba(0, 0, 0, 0.85)), rgba(255, 255, 255, 0.05);
    /* Transparent Gray */
    
    border: 1px solid rgba(117, 117, 117, 0.4);
    box-sizing: border-box;
    box-shadow: 0px 2px 21px rgba(0, 0, 0, 0.37);

    width: 500px;
    max-width: 500px;

    .header {

      align-items: center;
      justify-content: center;
      padding: 11px 24px;
      text-align: start;

      span {
        text-transform: uppercase;
        font-style: normal;
        font-weight: 500;
        font-size: 24px;
        line-height: 36px;
        color: #FFFFFF;
        text-align: left;
      }
    }

    .form {

      padding: 14px 32px 30px 32px;
      background: rgba(255, 255, 255, 0.1);
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

      .desc {
        text-align: left;
        font-size: 18px;
        line-height: 27px;
        opacity: 0.5;
        color: white;
        font-weight: 100;
      }

      section {
        display: flex;
        flex-direction: column;

        margin-top: 16px;

        span {
          text-align: left;
          font-style: normal;
          font-weight: normal;
          font-size: 18px;
          line-height: 27px;
          color: white;
        }

        .input {
          
          margin-top: 4px;
          font-family: Oswald;
          background: none;
          border: none;
          border-radius: 0;
          outline: none;
          -webkit-appearance: none;
          -moz-appearance: none;
          appearance: none;

          padding: 6px 16px 0px 16px;
          resize: none;
          font-weight: 300;
          background: rgba(255, 255, 255, 0.28);
          border-radius: 4px;
          line-height: auto;

          line-height: 30px;

          color: #FFFFFF;
          font-size: 20px;

          &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
            color: #FFFFFF;
            opacity: 0.5;
          }

        }

      }
    }
    
    .buttons {
      display: flex;
      flex-direction: row;

      margin-top: 30px;
      justify-content: space-between;

      text-transform: uppercase;

      cursor: pointer;
      
      div {
        padding: 6px 44px;
        font-family: Oswald;
        font-style: normal;
        font-weight: normal;
        font-size: 22px;
        line-height: 33px;

        color: #FFFFFF;
      }

      .var1 {
        background: rgba(255, 255, 255, 0.28);
        border-radius: 4px;
        transition: all 0.3s;

        &:hover {
          background: rgba(255, 255, 255, 0.48);
        }

      }

      .var2 {
        border: 1px solid rgba(255, 255, 255, 0.55);
        box-sizing: border-box;
        border-radius: 4px;
        transition: all 0.3s;

        &:hover {
          border: 1px solid rgba(255, 79, 79, 0.9);
        }

      }

    }
  }

}

.animation-enter {
  opacity: 0;
  transform: scale(0.9);
}
.animation-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 300ms, transform 300ms;
}
.animation-exit {
  opacity: 1;
}
.animation-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}
